                var section = $(this).closest(SECTION_SEL);

                if ($(this).hasClass(SLIDES_PREV)) {
                    if(isScrollAllowed.m.left){
                        moveSlideLeft(section);
                    }
                } else {
                    if(isScrollAllowed.m.right){
                        moveSlideRight(section);
                    }
                }
