                    direction: direction,
                    destinyPos: destiny.position(),
                    slideIndex: destiny.index(),
                    section: section,
                    sectionIndex: section.index(SECTION_SEL),
                    anchorLink: section.data('anchor'),
                    slidesNav: section.find(SLIDES_NAV_SEL),
